
















































.contact-wrap {
  padding: 0px 20px;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.contact-btn {
  text-align: center;
  width: 100%;
  padding: 20px 0px;
}

.contact-btn button {
  border: none;
  color: #ffffff;
  background: #265baa;
  width: 80%;
  line-height: 30px;
}

.contact-wrap h3 {
  line-height: 40px;
  font-size: 14px;
}

.contact-wrap textarea {
  display: block;
  width: 100%;
  height: 280px;
  border: 1px solid #999999;
}

.service-center {
  width: 100%;
  overflow: hidden;
  position: relative;
  padding: 10px 20px;
}

.service-center a {
  display: block;
  width: 20%;
}

.service-center a > img {
  display: block;
  width: 100%;
  width: 60px;
  height: 60px;
  border-radius: 100%;
}

.service-center a, .service-center .service-info {
  float: left;
}

.service-center .service-info {
  width: 80%;
  padding-left: 20px;
  line-height: 36px;
}

.service-center .service-info h3, .service-info p {
  color: #414a55;
  font-weight: normal;
  font-size: 12px;
}

.service-info p > .redn {
  font-weight: bold;
}

.blue {
  color: #265baa;
}

.contact-tabBar .myvux-swiper .parentnav-tabs li {
  width: 50%;
}



