.aftersales-items .l {
  width: 25%;
}

.aftersales-items .r {
  width: 75%;
}

.time {
  overflow: hidden;
  color: #6f6f6f;
}

.aftersales-itemdescription {
  margin-top: 8px;
}

.aftersales-itemdescription, .aftersales-items {
  padding: 15px 8px;
  background: #fff;
  overflow: hidden;
}

.aftersales-items {
  border-top: solid #e1e1e1 1px;
}

.aftersales-items .desc {
  padding-left: 10px;
}

.aftersales-items .desc a {
  padding-bottom: 5px;
  font-size: 14px;
}

.aftersales-itemdescription .l {
  color: #6f6f6f;
}

.aftersales-itemdescription .l, .aftersales-itemdescription .r {
  float: left;
  width: 50%;
}

.modal-header {
  padding: 0px 0 0 15px;
}

.modal-header .close {
  background: #265baa;
  color: #fff;
  padding: 8px 10px;
  opacity: 1;
}

.modal-title {
  padding: 5px;
  padding-right: 15px;
  text-align: center;
  color: #265baa;
}

.aftersales-itemdescription .btn {
  background: #fff;
  color: #275bab;
  border: #275bab solid 1px;
  font-size: 12px;
  margin-left: 5px;
}

.form-control {
  border: #275bab solid 1px;
}

.myvux-swiper .nav-tabs {
  background: #fff;
}

.myvux-swiper .nav-tabs > li > a {
  color: #989898;

}

.myvux-swiper .nav-tabs > li.active > a {
  color: #275bab;
}

.tab-content {
  background: #f5f5f5;
}

.global-red {
  color: #dc0000;
}

.myvux-swiper .parentnav-tabs li {
  width: 50%;
  text-align: center;
  background: #f5f5f5 !important;

}

.myvux-swiper .parentnav-tabs a {
  color: #275bab !important;
}

.text-center-logo2 {
  background: #f5f5f5;
}

.tab-content-three li {
  width: 33.33%;
  text-align: center;
}



