 }*/
.memberCenter {
  background-size: 100% 100%;
  padding-bottom: 10px;
  padding-top: .21rem;
  position: relative;
  background: #ffffff;
}

.content {
  padding: 0 .24rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  padding-bottom: .3rem;
  color: #fff;
}

.content .head-top {

  text-align: center;
}

.content .head-top > span {
  color: #424954;
  font-size: 15px;
  padding-top: 10px;

}

.content .head-top > img {
  width: 1.8rem;
  height: 1.8rem;
  display: block;
  border-radius: 50%;
  border: .04rem solid #f9b5b6;
}

.msg-num {
  position: absolute;
  border-radius: 50%;
  width: 0.42rem;
  line-height: 0.42rem;
  border: 1px solid #e83939;
  color: #e83939;
  position: absolute;
  top: 0;
  background: #fff;
  right: -7px;
}

.membername .p1 {
  color: #414a55;
}

.p2 {
  color: #414a55;

}

.p-text {
  color: #7c858d;

}

.content .person_i {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  padding-left: .2rem;
  padding-top: .25rem;
}

.area, .msg-box {
  position: relative;
}

.msg-box:nth-child(2) {
  margin-left: 10px;
}

.area {
  text-align: center;
}

.area span {

  color: #265baa;
  font-size: 15px;

}

.msg-box {
  float: left;

}

.area a {
  display: block;
  width: 35px;
  margin: 0 auto;
  position: relative;
}

.area-right p {
  display: inline-block;
  text-align: center;
  color: #265baa;
}

/*.area img {*/
/*width: 0.8rem;*/
/*height: 0.8rem;*/
/*}*/

.area > p {
  line-height: 0.68rem;
  background: #265baa;
  display: inline-block;
  color: #ffffff;
  padding: 0 15%;
  margin-top: 10px;
}

.area-right {
  display: block;
}

.h_bottom {
  margin: 0;
  border-top: 1px solid #DDD8CE;
  overflow: hidden;
  font-size: inherit;
  /* font-weight: 400; */
  position: relative;
  left: 0;
  bottom: 0;
  /* height: 1.8rem; */
  /* display: -webkit-box; */
  display: -ms-flexbox;
  /* display: flex; */
  /* -webkit-box-align: center; */
  -ms-flex-align: center;
  /* align-items: center; */
  width: 100%;
  text-align: center;
  font-size: .24rem;
  padding-top: 10px;
}

.h_bottom .mytabs .mytab-item {
  width: 33%;
}

.h_bottom .mytabs .mytab-item a {
  height: 24vw;
}



