






















































@import "~assets/bower_components/bootstrap/dist/css/bootstrap.css";

.mycustomer-main {
  background: #fff;
  padding-top: 20px;
}

.my-customer-services {
  overflow: hidden;
}

.my-customer-services .inforgroup .items {
  padding: 8px 0;
  border-bottom: #e4e4e4 solid 1px;
  color: #414a55;
}

.my-customer-services .apply-changeservice {
  margin-top: 20px;
}

.contact-her {
  border-bottom: #e4e4e4 solid 1px;
  border-top: #e4e4e4 solid 1px;
  padding: 15px;
  white-space: nowrap;
  text-align: center;

}

.contact-her a {
  width: auto;
  padding-left: 15px;
  padding-right: 15px;
  margin: auto 7px;
}

.my-customer-services .inforgroup label {
  width: 5em;
}

.my-customer-services .lll {
  padding-left: 15px;
}

.my-customer-services .l {
  width: 40%;
}

.my-customer-services .r {
  width: 60%;
  float: left;
}



