





























































































































@import "~assets/font-awesome/css/font-awesome.min.css";

#app {
  background: #f5f5f5;
}

.goods-info {
  border-bottom: 1px solid #cccccc;
}

.goods-tabBar .myvux-swiper {
  border-top: 1px solid #cccccc;
}

.goods-tabBar .myvux-swiper .parentnav-tabs li {
  width: 33.3%;
}

.goods-tabBar .tab-content {
  background: none;
}

.goods-tabBar .myvux-swiper .nav-tabs > li > a {
  color: #989898 !important;
  border-right: 1px solid #cccccc;
}

.goods-tabBar .nav-tabs > li > a {
  border-radius: 0;
  border: none;
}

.goods-tabBar .nav-tabs > li:last-child > a {
  border-right: none;
}

.goods-tabBar .nav-tabs > li:first-child > a {
  border-left: none;
}

.goods-tabBar .nav-tabs > li {
  margin-bottom: 0;
}

#app .goodsBar {
  background: #fff;
  padding-bottom: 20px;
  margin-bottom: 0;
}

.prod-comment {
  margin-bottom: 100px;
}

.comment-img-container {
  margin: 9px 0 1px 0;
  overflow: hidden;
  font-size: 0;
}

.comment-img-container .img-container {
  overflow: hidden;
}

.img-container .comment-img-item {
  display: inline-block;
  width: 88.75px;
  height: 88.75px;
  overflow: hidden;
  border-radius: 4px;
}

.comment-item-author {
  color: #265baa;
}

.product-item-star {
  display: inline-block;
  overflow: hidden;
  width: 75px;
  height: 14px;
  margin-top: 10px;
  margin-left: 20px;
  background: url("~assets/category/Favouritebt.png") repeat-x 0 0;
  background-size: 15px 30px;
}

.comment-item-content {
  line-height: 30px;
}

.comment-stars-width5 {
  width: 100%;
}

.product-item-star .real-star {
  display: inline-block;
  height: 22px;
  background: url("~assets/category/Favouritebt.png") repeat-x 0 0;
  background-size: 15px 30px;
}

.comment-list {
  padding: 20px;
}

.goods-eval {
  color: #000000;
  font-size: 15px;
  margin-right: 30px;
}

.lr {
  float: right;
  color: #6f6f6f;
}

i, em, a, strong, span {
  font-style: normal;
}

.commendetailModel {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;

}

.goodseval {
  border-bottom: 1px solid #e1e1e1;
  padding: 0 20px;
}

.goodseval .info {
  position: relative;
  padding-top: 15px;
  padding-bottom: 14px;
  line-height: 13px;
}

#s-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  font-size: .13rem;
}

.goods-tabBar {
  border-top: 1px solid rgba(0, 0, 0, .1);
  border-bottom: 1px solid rgba(0, 0, 0, .1);
  margin-top: 10px;
  overflow: hidden;
}

#s-header a {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
  display: block;
  -ms-flex-preferred-size: 1px;
      flex-basis: 1px;
  display: -webkit-box;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  display: -ms-flexbox;
  -ms-flex-pack: center;
  -ms-flex-align: center;
  -webkit-box-orient: vertical;
  -ms-flex-orient: vertical;
  height: 3em;
  color: #989898;
  border-top: 2px solid transparent;
  position: relative;
  font-size: 15px;
  background: #f5f5f5;
}

#s-header a.cur {
  background: #ffffff;
  color: #265baa;
}

#s-header a::after {
  content: '\20';
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  border-right: 1px solid #e5e5e5;
}

.item-top .share-icon {
  width: 20%;
  vertical-align: top;
  line-height: 26px;
}

.part-note-msg {
  display: inline-block;
  line-height: 15px;
  font-size: 13px;
  color: #81838e;
  width: 26px;
  white-space: normal;
  height: auto;
  float: left;
  padding-top: 20px;
  margin-right: 25px;
  color: #000000;
}

.quantity-wrapper {
  display: inline-block;
  height: 30px;
  border-radius: 5px;
  margin-left: 8px;
  float: left;
  padding: 20px 0;
}

.num-wrapper {
  height: auto;
}

.quantity-decrease, .quantity-increase {
  background: #ebebeb;
  border: 1px solid #cbcbcb;
  color: #232326;
  display: block;
  line-height: 24px;
  width: 30px;
  overflow: hidden;
  text-indent: -200px;
}

.quantity-decrease em {
  background: url("~assets/category/cart-number-new.png") no-repeat;
  background-size: 25px 25px;
  background-position: 0 -15px;
  height: 12px;
  width: 12px;
  display: block;
  margin: 6px;
  margin-left: 8px;
}

.num-wrapper .quantity-decrease em {
  height: 16px;
}

.num-wrapper .quantity {
  height: 30px;
}

.quantity {
  color: #232326;
  border: solid #cbcbcb;
  border-width: 1px 0 1px 0;
  height: 24px;
  width: 34px;
  border-radius: 0;
  -webkit-appearance: none;
}

.quantity-decrease, .quantity, .quantity-increase {
  float: left;
  font-size: 15px;
  text-align: center;
  height: 100%;
}

.quantity-increase {
  -webkit-border-radius: 0 2px 2px 0;
}

.quantity-increase em {
  background: url("~assets/category/cart-number-new.png") no-repeat 0 0;
  background-size: 25px 25px;
  height: 12px;
  width: 12px;
  display: block;
  margin: 6px;
  margin-top: 10px;
  margin-left: 8px;
}

.item-top {
  line-height: 35px;
}

.item-price {
  text-align: right;
}

.item-price span {
  color: #6f6f6f;
  font-size: 12px;
}

.item-price span em {
  font-style: normal;
}

.item-price span:nth-child(2) {
  margin-right: 10px;
}

.item-price span.fl {
  float: left;
  color: #e40000;
}

.item-top h2 {
  display: inline;
  color: #275bab;
  font-weight: normal;
  font-size: 15px;
}

.item-top span {
  float: right;
  text-align: right;
}

.item-top span > i {
  font-size: 18px;
  color: #275bab;
}

.item-top span i:nth-child(1) {
  margin-right: 5px;
}

.item-line {
  border-top: 1px solid rgba(0, 0, 0, .1);
  padding: 0 20px;
  padding-bottom: 8px;
  overflow: hidden;
}

.s-actionBar-container {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1;
}

.mui-flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.action-bar {
  color: #999;
  border-top: 1px solid #E5E5E5;
}

.action-bar .cell {
  color: #265baa;
}

.action-bar .btn2 {
  color: #ffffff;
}

.goods-tabBar .nav-tabs > li.active > a, .goods-tabBar .nav-tabs > li.active > a:hover, .goods-tabBar .nav-tabs > li.active > a:focus {
  border: none;
  border-right: 1px solid #cccccc;
}

.mui-flex > .cell {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: 0;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  max-width: 100%;
  display: block;
  padding: 0 !important;
  position: relative;
}

.action-bar .cell {
  height: 55px;
  text-align: center;
  border: none;
}

.action-bar .support {
  max-width: 65px;
  border-right: 1px solid rgba(0, 0, 0, .05);
  min-width: 26px;
}

.action-bar .toshop {
  max-width: 65px;
  min-width: 26px;
  border-right: 1px solid rgba(0, 0, 0, .05);
}

.action-bar .addfav {
  max-width: 65px;
  border-right: 1px solid rgba(0, 0, 0, .05);
  min-width: 26px;
}

.action-bar .cart {
  background: #FF9500;
}

.action-bar .buy {
  background: #DD2727;
}

.goods-swiper {
  height: 280px;
  overflow: hidden;
}

.goods-swiper .swiper-pagination {
  bottom: -1px;
}



