




































.login_main {
  margin: 0 auto;
  background: #ffffff;
  max-width: auto;
  font-size: 100%;
  margin-bottom: 0px;
  position: relative;
  width: auto;
  padding: .4rem 0.875rem 0.5rem;
  border-bottom: 1px solid #e5e5e5;
  box-shadow: 0px 2px 10px #eaeaea;
  z-index: 2;
}

.error {
  color: red;
}

.login_main form {
  -webkit-text-size-adjust: 100%;
  font-size: 100%;
}

.login_main label {
  display: inline-block;
  position: relative;
  width: 100%;
  padding-top: 0;
  overflow: hidden;
  margin-bottom: 5%;
}

.btn_refresh {
  display: inline;
  position: relative;
}

.login_main .fa {
  position: absolute;
  z-index: 9999;
  top: -3px;
  left: 0;
  right: 0;
  font-size: 25px;
  color: #265baa;
  text-align: center;

}

.login_main span {
  color: #6f6f6f;
  font-size: 15px;
  display: block;
  text-align: left;
  padding: 0px 0px 5px;
}

.login_main .icon_pic {
  width: 30%;
  display: inline-block;
  vertical-align: middle;
  padding: 0px 4%;
  overflow: hidden;
}

.login_main span:last-child {
  padding-top: 10px;
}

.login_main i {
  color: #ff4b4b;
}

.login_main input {
  border: 1px solid #999999;
  width: 100%;
  height: 48px;
  width: 100%;
  height: 100%;
  padding-left: 5px;
  box-sizing: border-box;
  height: 0.75rem;
}

.login_main .tips {
  font-size: 12px;
}

.login_main a {
  display: inline-block;
  width: 100%;
  padding: 0.655em 0;
  text-align: center;
  font-size: 1.25em;
  background: #265baa;
  margin-top: 0px;
  letter-spacing: .38em;
  color: #FFFFFF;
}

.login_main .inp_pic {
  width: 50%;
}

.login_main .inp_refresh {
  width: 17%;
  vertical-align: middle;
}

.login_main .inw {
  width: 45%;
}

.inw:first-child {
  float: left;
}

.inw:last-child {
  float: right;

}

.login_main input.inp_refresh, .inw:last-child {
  color: #345ca5;
  border: 1px solid #345ca5;
  background: #FFFFFF;
}

.login_main label:first-child {
  padding-bottom: 0 !important;
}




