














































.picture img {
  border-radius: 50%;
  width: 100px;
  height: auto;
}

.accountmanger-avaible {
  border-top: #e2e4e6 solid 1px;
  width: 100%;
  text-align: center;
  color: #265baa;
}

.accountmanger-avaible .active {
  background: #f7f7f7;
}

.accountmanger-avaible th {
  border-bottom: #e2e4e6 solid 1px;
  font-weight: normal;
  height: 1vw;
}

.accountmanger-avaible th, .accountmanger-avaible td {
  width: 33.3%;
  padding: 0;
}

.accountmanger-avaible th a, .accountmanger-avaible td a {
  padding: 24px 0;
  width: 100%;
  height: 30vw;
  z-index: 2;
}

.accountmanger-avaible span {
  display: block;
}

.accountmanger-avaible td {
  border-left: #e2e4e6 solid 1px;
  border-bottom: #e2e4e6 solid 1px;
}

.accountbtn img {
  width: 50px;
}

.mycustomer-main {
  background: #fff;
  padding-top: 10px;
}

.my-customer-services {
  overflow: hidden;
  margin-bottom: 15px;
}

.contact-her {
  border-bottom: #e4e4e4 solid 1px;
  border-top: #e4e4e4 solid 1px;
  padding: 15px 0 40px;
  white-space: nowrap;
  text-align: center;
}

.contact-her2 {
  border-top: none;
}

.contact-her a {
  width: auto;
  padding-left: 15px;
  padding-right: 15px;
  margin: auto 7px;
}

.my-customer-services .inforgroup .itemss label {
  width: auto;
  padding: 2px 0;
  font-size: 13px;
}

.my-customer-services .inforgroup .itemss label b {
  font-size: 18px;
  font-weight: normal;
  line-height: 18px;
  margin-top: 5px;
}

.my-customer-services .lll {
  padding-left: 15px;
}

.my-customer-services .l {
  width: 30%;
}

.my-customer-services .r {
  width: 70%;
  float: left;
}

.contact-her a.logoutbtn {
  background: #6fa4d8;
  width: 80%;
  border-radius: 0;
}

.contact-her a.logoutbtn:active {
  background: #265baa;
}



