



























.messages * {
  vertical-align: middle;
}

.messages .items {
  padding: 8px 10px;
  border-bottom: #e1e1e1 solid 1px;
}

.messages b {
  color: #000;
  font-weight: normal;
  font-size: 16px;
  white-space: nowrap;
  overflow: hidden;
  max-width: 50%;
  text-overflow: ellipsis;
}

.messages i {
  color: #a0a0a0;
  font-size: 10px;
}

.messages .p {
  padding-top: 10px;
  color: #6f6f6f;
  white-space: nowrap;
  overflow: hidden;
  max-width: 100%;
  text-overflow: ellipsis;
}

.message-desc {
  position: relative;
  width: 78%;
}

.message-desc i {
  position: absolute;
  right: 0;
  top: 0;
}

.messages img {
  max-width: 20%;
}

.messages b {
  overflow: hidden;
}




