


































































































@import "~assets/font-awesome/css/font-awesome.css";
@import "~assets/swiper/dist/idangerous.swiper.css";
@import "~assets/swiper/dist/animate.min.css";

.webside-top {
  overflow: hidden;
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  z-index: 10;
}

.webside-top .lf {
  float: left;
  width: 20%;
}

.mytabs {
  text-align: center;
  white-space: nowrap;
  color: #265baa;
  padding: 15px 0;
}

.mytabs .mytab-item {
  display: inline-block;
  width: 23%;
  text-align: center;
}

.mytabs span {
  display: block;
}

.mytabs .mytab-item {
  position: relative;
}

.mytabs .mytab-item a {
  border-radius: 50%;
  background: #6fa4d8;
  display: inline-block;
  width: 60.87%;
  height: 14vw;
  position: relative;

}

.mytabs .mytab-item a img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.webside-top .fr {
  width: 80%;
  float: right;
}

.search-section {
  margin-top: 10px;
  border: #275bab solid 1px;
  display: block;
  color: #275bab;
  margin-right: 12px;
  white-space: nowrap;
}

.vux-swiper-item img {
  width: 100%;
}

.search-section i {
  vertical-align: middle;
}

.search-section input {
  height: 25px;
  line-height: 25px;
  display: inline-block;
  width: 87%;
  vertical-align: middle;
  outline: none;
}

.logo {
  padding: 10px;
  padding-left: 12px;
}

.swiper-container {
  height: 200px;
  width: 100%;
}

.swiper-container .slider-bgs {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  position: relative;
}

.swiper-pagination {
  width: 100%;
  display: block;
  text-align: center;
  z-index: 100;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
}

.swiper-pagination-switch {
  width: 15px;
  height: 2px;
  background: #6fa4d8;
  margin: 0 5px;
  display: inline-block;
}

.swiper-active-switch {
  background: #265baa;
}

.title-slider .titles-bgs {
  max-width: 50%;
}

.title-slider {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

.title-slider div {
  margin-bottom: 5px;
}

.title-slider .text-center a {
  background: #265baa;
  color: #fff;
  padding: 5px 30px;
  font-size: 12px;
}

.tabbar-fixed {
  position: fixed !important;
}

.hiddens {
  visibility: hidden !important;
}
.mytabs .mytab-item a.active {
  background: #265baa;
}




