



















































.product-list {
  text-align: center;
  overflow: hidden;
  padding: 0 3%;
}

.product-list li {
  padding: 10px 0;
  float: left;
  width: 48%;
  margin-right: 4%;
  margin-top: 10px;
  background: #fff;
  list-style: none;
}

.product-list li:nth-child(2n) {
  margin-right: 0;
}

.product-list li a {
  display: block;
}

.product-list li img {
  width: 80%;
}

.product-list li div {
  color: #275bab;
  line-height: 30px;
}

.product-list li p {
  color: #e40000;
  margin-bottom: 0;
}



