








































body {
  background: none;
}

.right-top {
  padding: 10px 4%;
  margin-top: 12px;
}

.right-top img {
  width: 100%;
  margin-bottom: 6px;
}

.right-mid {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  padding: 12px 0;
  margin: 10px 0;
}

.right-mid li {
  width: 33.33%;
  text-align: center;
}
.right-mid li:first-child span {
  width: 30px;
  text-align: center;
  background: url("~assets/usercenter/jifeng-icon-80x80.png") no-repeat;
  background-size: 30px 30px;
  padding-top: 32px;
  vertical-align: middle;
}

.right-mid li:first-child strong {
  font-size: 18px;
  vertical-align: middle;
}

.right-mid li:nth-child(2) {
  text-align: left;
}

.right-mid li:nth-child(2) span {
  font-size: 15px;
  margin-bottom: 5px;
}

.right-mid li:nth-child(3) {
  padding: 0 12px;
}

.get {
  padding: 0 25%;
}

.right-list-1 {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
}

.right-list-1 li {
  float: left;
  width: 33.33%;
  border-right: 1px solid #ddd;
  color: #888;
}
.right-list-1 li:first-child {
  color: #275bab;
}

.right-list-1 li:last-child {
  border: none;
}

.right-list-2 {
  padding: 12px 0;
}
.right-list-2 .col-1{
  font-size: 15px;
}
.right-list-2 li {
  width: 50%;
  padding: 0 10px;
}
.right-list-2 p{
  margin-bottom: 5px;
}
.right-list-2 .title {
  font-size: 15px;
  margin-bottom: 5px;
}
.box-btn{
  margin: 10px 10px 0 0;
}



