


























































































































































.reserve {
  padding: 10px 15px;
}

.reserve-title {
  color: #000000;
  padding-bottom: 5px;
}

.reserve-box {
  background: #ffcccc;
  border: 1px solid #ff0000;
  color: #e40000;
  font-size: 12px;
  padding: 10px;
}

.reserve-title span {
  float: right;
}

.bgf5 {
  background: #f5f5f5;
}

.total-ul, .total-ul a {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.total-ul {
  border-top: 1px solid #999999;
}

.total-ul a {
  display: block;
  border-right: 1px solid #999999;
  border-bottom: 1px solid #999999;
  box-sizing: border-box;
  display: -webkit-box;
}

.total-ul a:nth-child(3) {
  border-right: none;
}

.total-ul .icon {
  position: absolute;
  width: 20px;
  height: 20px;
  top: -12px;
  right: 0px;
  display: none;
}

.total-ul .active {
  display: block;
}

.total-ul li {
  width: 33.3%;
  float: left;
}

.total-line {
  text-align: center;
  line-height: 40px;
}

.txt-fr {
  text-align: right;
}

.bz {
  padding: 12px 15px 10px 15px;
  width: 100%;
  overflow: hidden;
}

.bt {
  border-bottom: 1px solid #999999;
}

.b1 {
  width: 20%;
  float: left;
  width: 20%;
  display: inline-block;
  line-height: 12px;
}

.b2 {
  float: right;
  width: 80%;

}

.b2 textarea {
  width: 100%;
  height: 85px;
  resize: none;
}

textarea {
  border: 1px solid #999999;
}

.make-sure-order-line {

  overflow: hidden;
}

.lh {
  /*line-height: 42px;*/
  float: left;
  position: absolute;
  top: 10px;
}

.fr {
  float: right;
}

.order-send span {
  color: #000000;
  padding-left: 10px;
}

.make-sure-container {
  margin-top: 10px;
  border-top: 1px solid #e1e1e1;
}

.pro-send {
  float: right;
  text-align: right;
  padding-top: 10px;
}

.make-sure-item li {
  padding: 10px 15px;
}

.make-sure-item .title-line {
  line-height: 18px;
  height: 18px;
  margin-top: 11px;
  overflow: hidden;
}

.make-sure-item .title-line .title {
  float: left;
  max-width: 100%;
  line-height: 18px;
  height: 18px;
  font-size: 0;
}

.make-sure-item .title-line .store {
  float: right;
  color: #dc0000;
  font-weight: bold;
  font-size: 12px;
  -webkit-transform: scale(.91666667);
  transform: scale(.91666667);
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
}

.make-sure-item .title-line .title .item-name {
  display: inline-block;
  max-width: 100%;
  font-size: 15px;
  color: #275bab;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.make-sure-item .cart-right {
  width: 100%;
  position: relative;
}

.make-sure-item .cart-right .cart-img {
  width: 28%;
  height: 100px;
}

.make-sure-item .shopcart-wrapper {
  padding-bottom: 0px;
}

.make-sure-item b {
  float: right;
}

.pro-box h3 {
  overflow: hidden;
}

.black {
  color: #000000;
}

.address-cir {
  background: #265baa;
  padding: 5px;
  float: left;
  margin: 0px 10px;
}

.weui_cell_bd > p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #6f6f6f;
}

.vux-label-desc {
  margin-top: 3px;
}

.vux-label-desc > span {
  color: #265baa;
}

.vux-label-desc > span:nth-child(1) {
  margin-right: 10px;
}

.address-icon {
  border-radius: 100%;
  border: 2px solid #fff;
  background: #6fa4d8;
  display: block;
  width: 32px;
  line-height: 32px;
  color: #fff;
  text-align: center;
}

.makesurewrap {
  margin-top: 0px;
  border-top: none;

}

.makesurewrap:before {
  content: '';
  border-top: 1px solid #bebebe;
}

.makesurewrap .nav-tabs > li.active > a {
  color: #265baa !important;
}

.weui_cell_ft.with_arrow:after {
  content: " ";
  display: inline-block;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  height: 10px;
  width: 10px;
  border-width: 1px 1px 0 0;
  border-color: #265baa;
  border-style: solid;
  position: relative;
  top: -1px;
  margin-left: .3em;
}



