





















































.detail-list {
  border-top: 1px solid #e4e6e7;
}

.mr {
  margin-right: 20px;
}

.detail-list dl {
  border-bottom: 1px solid #e1e1e1;
  padding: 15px 15px;
  width: 100%;
  text-align: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-align: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  overflow: hidden;
}

.detail-list dl dt {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  display: inline-block;
  width: 50%;
  white-space: nowrap;
  float: left;
  color: #6f6f6f;
  margin-right: 16px;
  font-size: 12px;
}

.detail-list dl dd {
  float: left;
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  color: #6f6f6f;
  font-size: 12px;
  overflow: hidden;
}

.green {
  color: #6af87a;
}

.wrap2 {
  margin-top: 0px;
  border-bottom: 1px solid #cccccc;
  margin-bottom: 10px;
}

.integral-box .flex-demo:nth-child(3), .integral-box .flex-box {
  width: auto;
}

.integral-box .flex-img {
  width: 50%;
}

.integral-box .flex-box h3 {
  font-size: 14px;
  padding-bottom: 6px;
  color: #275bab;
}

.integral-box .flex-demo:nth-child(1) {
  width: 48px;
}

.integral-box .flex-txt:nth-child(3) {
  width: auto;
}

.integral-box .flex-txt:nth-child(1) {
  width: auto;
}

.integral-box .flex-txt .flex-box {
  text-align: left;
  width: 127%;
}

.integral-box .vux-flexbox-item:nth-child(1) {
  border: none;
}

.integral-box .vux-flexbox-item .item-box {
  overflow: auto;
}

.integral-box .vux-flexbox-item .item-box-txt {
  width: 127%;
}

.integral-box .flex-box p {
  font-size: 12px;
  color: #7f7f7f;
}

.integral-box .flex-box button {
  border: none;
  background: #275bab;
  color: #ffffff;
  padding: 10px 0;
  width: 70px;
}

.integral-box .item-btn .flex-demo {
  width: 70px;
}

.integral-box .vux-flexbox-item .item-btn {
  width: 70px;
}

.item-btn {
  width: 70%;
}

.integral-box .vux-flexbox-item:nth-child(1) .flex-demo:nth-child(2) {
  font-size: 16px;
  padding-top: 22%;
}



