



























































.j-brand {
  width: 100%;
  padding: 10px 5%;
}

.j-brand-info {
  width: 90%;
}

.j-header .j-brand > input {
  width: 100%;
  margin: 0;
}

.j-header .brand-info {
  width: 100%;
}

.img-box {
  list-style: none;
  padding: 0 10px;
  padding-top: 5px;
  overflow: hidden;
  background: #f5f5f5;
}

.img-box li {
  float: left;
  width: 33%;
  position: relative;
  padding-left: 5px;
  padding-right: 5px;
  box-sizing: border-box;
  margin-top: 10px;

}

.img-box li a {
  background: #ffffff;
  position: relative;
  border: 1px solid #cccccc;
}

.img-box li a img {
  width: 100%;
  display: block;
}

.img-box li a span {
  position: absolute;
  bottom: 8px;
  left: 0;
  right: 0;
  margin: 0 auto;
  text-align: center;
  font-family: arial;
  font-size: 16px;
}



