






















































































.ph {
  margin-top: 10px;
}

.order-orderPay {
  background-color: #fff;
  color: #999;
  padding: 9px;
  line-height: 2;
}

.order-orderPay .line {
  text-align: right;
  color: #000;
}

.order-orderPay .line > span:nth-child(1) {
  margin-right: 10px;
}

.cart-item {
  overflow: hidden;
}

.cart-item .cart-box {
  position: relative;
}

.cart-right {
  display: inline-block;
  width: 90%;
  vertical-align: top;
}

.cart-right .cart-img {
  width: 32%;
  float: left;
  padding: 0 15px;
  padding-left: 0px;
  height: 170px;
  overflow: hidden;
}

.pr {
  position: relative;
  line-height: 22px;
  display: inline-block;
  width: 22px;
  margin: 72px 0;
}

.pro-box h3 {
  color: #275bab;
  font-size: 14px;
  padding-top: 6px;
}

.cart-item li {
  position: relative;
  padding: 20px;
  border-bottom: 1px solid #ddd;
  background: #ffffff;
}

.cart-item input[type=checkbox] {
  visibility: hidden;
}

.cart-item .label {
  cursor: pointer;
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  bottom: 0;
  background: #eee;
  border: 1px solid #ddd;
  display: block;
}

.cart-item input[type=checkbox]:checked + .label:after {
  content: '';
  position: absolute;
  width: 9px;
  height: 5px;
  background: transparent;
  top: 5px;
  left: 3px;
  border: 3px solid #333;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  opacity: 1;
}

.shopcart-wrapper {
  padding-left: 0px;
  margin-left: 0px;
}



