























































































































































.today-title span {
  float: right;
  border: 1px solid #275bab;
  color: #275bab;
  line-height: 30px;
  padding: 0px 17px;
  cursor: pointer;
}

.today-wrapper {
  border-bottom: 1px solid #e4e6e7;
}

.today-title {
  padding: 10px 12px;
  font-size: 15px;
  line-height: 30px;
  overflow: hidden;
}

.today-title h4 {
  font-weight: 400;
  color: #000000;
  float: left;
}

.today-container {
  padding: 0 7px
}

.today-container ul {
  list-style: none;
  overflow: hidden;
}

.today-container li {
  float: left;
  width: 50%;
  padding: 0 5px 10px;
  box-sizing: border-box;
  overflow: hidden;
}

.hot-container li {
  width: 33.3%;
}

.today-info-container {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.today-container li a {
  padding: 0 5px;
  display: block;
  border: 1px solid #cccccc;
  text-align: center;
}

.today-img-container {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.today-info-container h3 {
  margin-top: 8px;
  line-height: 20px;
  font-size: 16px;
  color: #275bab;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.item-wid {
  width: 99px;
}

.mall-box .flex-img {
  width: 70%;
}

.wrap2 {
  overflow: hidden;
  width: 100%;

}

.mall-box, .mall-box .vux-flex-row {
  width: 100%;
}

.wrap .mall-box .vux-flex-row {
  padding-bottom: 20px;
  overflow: hidden;
}

.mall-box .img-box2 {
  width: 14%;
  overflow: hidden;
}

.mall-box .item-btn .flex-demo {
  width: 130%;
}

.mall-box .fl {
  float: left;
}

.mall-box .flex-demo {
  font-size: 14px;
  padding-top: 15%;
}

.mall-box .pt {
  padding-top: 38%;
}

.mall-box .flex-txt .flex-box {
  width: 100%;
}

.mall-box .flex-img-pt {
  padding-top: 24%;
}



