

















































































.star-list {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  background: #ffffff;
  padding-bottom: 10px;
  overflow: hidden;
}

.star-list li {
  height: 38px;
  position: relative;
}

.star-list nav {
  position: absolute;
  left: 88px;
  top: 0px;
}

.star-list nav a {
  float: left;
  background: url(../assets/category/Favouritebt.png) no-repeat 0 0;
  width: 34px;
  height: 34px;
  margin-right: 10px;
  -webkit-transition: .3s;
  transition: .3s;
  background-size: contain;
  background-position: 11px 17px;
}

/*.star-list nav a.active {*/
/*background-position: 11px -15px;*/
/*}*/

.upload-img, .upload-img a {
  position: relative;
  overflow: hidden;
}

.upload-img a {
  border: 1px dashed #275bab;
  padding: 30px 20px;
  display: inline-block;
}

.ugc-bar {
  width: 100%;
  position: relative;
  padding: 10px 20px;
  text-align: left;
  border-bottom: 1px solid #cccccc;
  overflow: hidden;
}

.ugc-img {
  float: left;
  position: relative;
  width: 80px;
  overflow: hidden;
}

.ugc-img img {
  display: block;
  width: 80px;
}

.ugc-info {
  position: relative;
  margin-left: 100px;
}

.ugc-info h2 {
  line-height: 35px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #275bab;
  font-size: 15px;
}

.ugc-info p:nth-child(2) {
  color: #000;
}

.ugc-info p:nth-child(3) {
  text-align: right;
}

.ugc-bar .black {
  color: #000;
}

.fl {
  float: left;
}

.order-num {
  float: right;
}

.scoreList {
  padding: 0 20px;
}

.scoreList li span {
  padding-top: 15px;
}

.scoreList li nav a:active {
  background-position: 11px -15px;
}



