.sales-wrap {
  margin-top: 10px;
  border-top: 1px solid #e1e1e1;
}

.upload-img a {
  color: #265baa;
  border-color: #265baa;
}

.contact-wrap textarea {
  resize: none;
  padding: 5px;
  height: 190px;
}

.ugc-bar h3 {
  line-height: 40px;
  font-size: 14px;
}

.refund {
  height: 35px;
  border: 1px solid #999999;
}

.selbox {
  width: 170px;
  position: relative;
  background-color: #f5f5f5;
}

.selbox:after {
  content: "";
  position: absolute;
  top: 11px;
  right: 6px;
  width: 0;
  height: 0;
  border: 8px solid transparent;
  border-top-color: #666;
}

.selbox > input {
  width: 100%;
  height: 30px;
  text-indent: 6px;
  border: 1px solid #999999;
}

.selbox > div {
  position: absolute;
  width: 100%;
  top: 36px;
  left: 0;
  background-color: #f5f5f5;
  z-index: 2;
}

.selbox > div > label {
  display: block;
  cursor: pointer;
  text-indent: 10px;
  padding: 8px 0;
  margin: 0;
}



