





















































































































































































































.j-order .j-header {
  border-bottom: none;
}

.j-list {
  margin-top: 10px;
  background: #f5f5f5;
}

.j-item {
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
}

.j-item .j-itemsales {
  padding: 5px 20px;
  padding-bottom: 10px;
}

.j-item .txt {
  width: 90%;
  padding: 12px 5%;
}

.aftersales-itemdescription .goto {
  border-radius: 0;
  font-family: microsoft yahei;
  padding: 0px;
  font-size: 12px;
  width: 45%;
  line-height: 32px;
}

.aftersales-itemdescription .receipt {
  color: #dc0000;
  background: #ffffff;
  border-color: #dc0000;
}

.aftersales-itemdescription .toPay {
  background: #dc0000;
  color: #ffffff;
  border-color: #dc0000;

}

.j-item .contact-service {
  background: #265baa;
  border-color: #265baa;
  color: #ffffff;
}

.j-item .aftersales-itemdescription .l {
  width: 50%;
}

.j-item .aftersales-itemdescription .r {
  width: 50%;
}

.j-item .sales {
  border-color: #265baa;
  background: #ffffff;
  color: #265baa;
}

.j-item .comment {
  background: #6fa4d8;
  border-color: #6fa4d8;
  color: #ffffff;
}

.j-item .view-status {
  background: #05c933;
  border-color: #05c933;
  color: #ffffff;
}

.j-item .appeal {
  background: #f2ac06;
  border-color: #f2ac06;
  color: #ffffff;
}

.b-color {
  color: #000000;
  font-size: 12px;
}

.j-item {
  margin-bottom: 10px;
  background-color: #fff;
}

.j-item .poi-head {
  display: block;
  border-bottom: 1px solid #e0e0e0;
}

.j-item .img-wrapper {
  float: left;
  position: relative;
  width: 84px;
  overflow: hidden;
}

.img-wrapper img {
  display: block;
  width: 108px;
}

.j-item .info-wrapper {
  position: relative;
  margin-left: 96px;

}

.txt > a {
  padding: 0px;
  width: 100%;
}

.j-item .desc {
  line-height: 25px;
  height: 25px;
  font-size: 14px;
  color: #275bab;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.j-item .price-data {
  color: #6f6f6f;
}

.j-item .price-data > span {
  float: right;
  font-size: 12px;
}

.j-item .price-line {
  line-height: 16px;
  height: 16px;
  font-size: 0;
  color: #ff4683;
  overflow: hidden;
}

.j-order .vux-tab .vux-tab-item.vux-tab-selected {
  color: #275bab !important;
}

.h45 {
  height: 45px;
}

.j-order .vux-tab .vux-tab-item {
  color: #989898 !important;
}



