












































































































































@import "~assets/bower_components/bootstrap/dist/css/bootstrap.css";

.personData {
  padding: 12px 8%;
}
.form-img{
  margin-bottom: 15px;
}
.form-img .picture{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  overflow: hidden;
}
.form-img img{
  width: 100%;
  height: 100%;
}
.form-img .r{
  padding-top: 15px;
  width: 80px;
}
.form-phone{
  margin-bottom: 15px;
}
.form-phone .col-2{
  padding-top: 4px;
}
.form-phone .r{
  width: 80px;
}
.sex label{
  display: block;
  float: left;
  width: 50%;
  line-height: 38px;
  text-align: center;
}

.sex label input{
  display: none;
}
.sex label span{
  display: block;
  border: 1px solid #ddd;
}
.sex label input:checked + span{
  background: #265baa url("~assets/home/select.png") no-repeat 30% center;
  background-size: 16px 16px;
  border: 1px solid #265baa;
  color: #fff;
}
.personData .form-group p{
  margin-top: 5px;
  color: #b1b1b1;
  font-size: 12px;
}
.personData .form-group > label{
  color: #666;
  margin-bottom: 5px;
}
.personData select{
  width: 80px;
  height: 32px;
}
.personData .select-1{
  width: 60px;
}
.modal-file{
  display: block;
  text-align: center;
}
.modal-file input{
  display: none;
}
.modal-file img{
  width: 100%;
  height: 100%;
}
.margin-t{
  margin-top: 8px;
}
.checkbox label{
  margin-right: 15px;
  margin-bottom: 10px;
}
.checkbox label input{
  position: relative;
  top: 6px;
}
.modal-footer{
  text-align: center;
}



