































.order-box {
  position: relative;
  padding-bottom: 10px;
}

.wrap h2 {
  line-height: 48px;
  font-weight: normal;
  font-size: 15px;
  color: #414a55;
 float: left;

}

.order-box .weui_tabbar {
  position: relative;
}

.order-box .weui_tabbar {
  background: none;
}

.order-box .weui_tabbar:before {
  -webkit-transform: scaleY(0.3);
  transform: scaleY(0.3);
}

.weui_tabbar_icon {
  width: 35px;
  height: 35px;
}




